
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { mapState, mapActions } from 'vuex'
export default {
name: 'app',
provide () {
return {
reload: this.reload
}
},
data(){
return{
direction : 'forward',
isRouterAlive: true
}
},
directives: {
TransferDom
},
components: {
ViewBox,
XHeader,
Tabbar,
TabbarItem,
},
created(){
if (sessionStorage.getItem('store')) {
this.$store.replaceState(Object.assign({}, this.$store.state, JSON.parse(sessionStorage.getItem('store'))))
}

window.addEventListener('beforeunload', () => {
sessionStorage.setItem('store', JSON.stringify(this.$store.state))
})
},
computed: {
headerTransition () {
return 'vux-header-fade-in-left'
},
title () {
if (this.$route.path === '/') return '登录'














return this.$route.name
},
isTeach () {
if (this.$store.getters.type == 'teacher') {
return true
}
return false
},
isStudent () {
if (this.$store.getters.type == 'student') {
return true
}
return false
},
isSupervisor () {
if (this.$store.getters.type == 'supervisor') {
return true
}
return false
},
},
methods: {
reload () {
this.isRouterAlive = false
this.$nextTick(function () {
this.isRouterAlive = true
})
}
}
}



